.baseInfo{
	display: flex;
	margin-top: 10px;

	&__detail{
		flex: 1;
	}

	&__poster{
		width: 100px;
		height: 150px;
		background-size: cover;
	}

	&__name{
		color: #fff;
		font-size: 18px;
		line-height: 28px;
		font-weight: 400px;
		margin: 0 0 8px;
	}

	&__subName,
	&__other{
		color: rgba(#fff, .7);
		font-size: 14px;
	}

	&__subName{
		margin-bottom: 14px;
	}

	&__other{
		margin-top: 8px;
	}
}
