.cityLayer {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  background-color: #fff; }
  .cityLayer__title {
    position: absolute;
    top: 0;
    width: 100%;
    height: 44px;
    line-height: 44px;
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    border-bottom: 1px solid #e2e2e2; }
  .cityLayer__close {
    position: absolute;
    width: 44px;
    height: 44px;
    text-align: center;
    font-weight: normal; }
  .cityLayer__content {
    position: absolute;
    top: 44px;
    bottom: 0;
    background-color: #f5f5f5;
    width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch; }
    .cityLayer__content .cityBlock {
      padding: 10px 15px; }
      .cityLayer__content .cityBlock__label {
        font-size: 12px;
        color: #9f9fa0;
        margin-bottom: 14px; }
      .cityLayer__content .cityBlock__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between; }
      .cityLayer__content .cityBlock__item {
        width: 116px;
        height: 32px;
        line-height: 32px;
        background-color: #fff;
        border: 1px solid #e4e2e2;
        color: #999;
        margin-bottom: 10px;
        text-align: center; }
    .cityLayer__content .cityList__label, .cityLayer__content .cityList__item {
      color: #333;
      font-size: 12px; }
    .cityLayer__content .cityList__label {
      height: 28px;
      line-height: 28px;
      padding: 0 15px;
      border: 1px solid #e4e2e2;
      border-bottom: 1px solid #e4e2e2; }
    .cityLayer__content .cityList__wrap {
      margin: 0;
      padding: 0 15px;
      background-color: rgba(255, 255, 255, 0.6); }
    .cityLayer__content .cityList__item {
      list-style: none;
      height: 48px;
      line-height: 48px; }
      .cityLayer__content .cityList__item:nth-child(n + 2) {
        border-top: 1px solid #e4e2e2; }
  .cityLayer__index {
    position: fixed;
    padding: 0 6px;
    top: 80px;
    right: 0; }
  .cityLayer .cityIndex__list {
    width: 40px;
    padding: 0;
    list-style: none; }
  .cityLayer .cityIndex__item {
    list-style: none;
    margin-bottom: 4px;
    text-align: center; }
    .cityLayer .cityIndex__item a {
      text-decoration: none;
      font-size: 12px;
      color: #5fb5f9; }
