//通用样式

//变量
$black: #000;
$gray: #777;
$border: #e4e2e2;
$active: #ef819e;

//混合器
@mixin flex-between{
	display: flex;
	justify-content: space-between;
}

@mixin flex-center{
	display: flex;
	justify-content: center;
	align-items: center;
}

@mixin flex-v-center{
	display: flex;
	flex-direction: column;
	justify-content: center;
}

@mixin line-ellipsis{
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}
