@import '../../util';

.seat{
	&::-webkit-scrollbar{
		display: none;
	}
	.tOperator{
		padding: 0 20px;
	}

	&__tip {
    height: 24px;
    padding: 10px 0;
    background: #fff url("../../icons/seats.svg") 50% 50% / auto 24px no-repeat;
  }

  &__graph {
    background-color: #F7F7F7;
    width: 100%;
		height: 500px;
		&::-webkit-scrollbar{
			display: none;
		}
  }

  &__screen {
    font-size: 14px;
    height: 28px;
    line-height: 28px;
    color: $black;
    text-align: center;
    background: transparent url("../../icons/screen.svg") 50% 0 / auto 28px no-repeat;
  }

  &__map {
    width: 100%;
		overflow-x: scroll;

		&::-webkit-scrollbar{
			display: none;
		}
  }
}
